<template>
  <div class="token-info-bar do-invert" :class="{ 'hidden': isHidden }">
    <div class="token-info-container">
      <div class="token-info-left">
        <div class="token-price-container">
          <i class="fas fa-coins token-icon"></i>
          <span class="token-price" v-if="tokenData.price">
            ${{ formatPrice(tokenData.price) }}
          </span>
          <span class="loading-text" v-else>Loading...</span>
        </div>
        <div class="token-stats">
          <div class="stat-item" v-if="tokenData.marketCap">
            <i class="fas fa-chart-area"></i>
            <span class="stat-label">MC:</span>
            <span class="stat-value">${{ formatMarketCap(tokenData.marketCap) }}</span>
          </div>
          <div class="stat-item" v-if="tokenData.holderCount">
            <i class="fas fa-users"></i>
            <span class="stat-label">Holders:</span>
            <span class="stat-value">{{ formatNumber(tokenData.holderCount) }}</span>
          </div>
        </div>
      </div>
      
      <div class="token-info-right">
        <button 
          class="buy-now-btn" 
          @click="openBuyModal"
          :disabled="loading"
        >
          <i class="fas fa-shopping-cart"></i>
          <span>Buy Now</span>
        </button>
      </div>
    </div>
    

  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue';

const emit = defineEmits(['open-buy-modal']);

const tokenData = ref({
  price: null,
  marketCap: null,
  holderCount: null
});

const loading = ref(true);
const isHidden = ref(false);
let updateInterval = null;

const TOKEN_ADDRESS = '4j9bDg7iWNah1Qa61rrqwWZMtEdqV3fV56SzyhfNpump';
const API_KEY = '22e8b5cbaed3418681e9beb529fdf443';

// Fetch token data from Birdeye API
const fetchTokenData = async () => {
  try {
    const response = await fetch(`https://lite-api.jup.ag/tokens/v2/search?query=${TOKEN_ADDRESS}`, {
    });
    
    if (response.ok) {
      const data = await response.json();
      if (data[0]) {
        tokenData.value = {
          price: data[0].usdPrice,
          marketCap: data[0].mcap,
          holderCount: data[0].holderCount
        };
      }
    } else {
      console.error('Failed to fetch token data:', response.status);
    }
  } catch (error) {
    console.error('Error fetching token data:', error);
  } finally {
    loading.value = false;
  }
};

// Format price
const formatPrice = (price) => {
  if (price >= 1) {
    return price.toFixed(4);
  } else {
    return price.toFixed(6);
  }
};

// Format market cap
const formatMarketCap = (marketCap) => {
  if (marketCap >= 1000000000) {
    return (marketCap / 1000000000).toFixed(2) + 'B';
  } else if (marketCap >= 1000000) {
    return (marketCap / 1000000).toFixed(2) + 'M';
  } else if (marketCap >= 1000) {
    return (marketCap / 1000).toFixed(0) + 'K';
  } else {
    return marketCap.toFixed(0);
  }
};

// Format number
const formatNumber = (num) => {
  if (num >= 1000000) {
    return (num / 1000000).toFixed(1) + 'M';
  } else if (num >= 1000) {
    return (num / 1000).toFixed(0) + 'K';
  } else {
    return num.toString();
  }
};

// Modal functions
const openBuyModal = () => {
  emit('open-buy-modal');
};

// Handle scroll to hide/show info bar
const handleScroll = () => {
  if (window.scrollY > 200) {
    isHidden.value = true;
  } else {
    isHidden.value = false;
  }
};

// Lifecycle
onMounted(() => {
  // Initial fetch
  fetchTokenData();
  
  // Update every minute
  updateInterval = setInterval(fetchTokenData, 60000);
  
  // Add scroll listener
  window.addEventListener('scroll', handleScroll);
});

onUnmounted(() => {
  if (updateInterval) {
    clearInterval(updateInterval);
  }
  
  // Remove scroll listener
  window.removeEventListener('scroll', handleScroll);
});
</script>

